.docs-page {
  --docs-font-family: var(--font-family, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif);
  --docs-font-size-base: var(--font-size-md, 1rem);
  --docs-font-size-sm: var(--font-size-sm, 0.875rem);
  --docs-font-size-xs: var(--font-size-xs, 0.8125rem);
  --docs-font-size-lg: var(--font-size-lg, 1.125rem);
  --docs-font-size-xl: var(--font-size-xl, 1.35rem);
  --docs-font-size-2xl: var(--font-size-xxl, 1.6rem);
  --docs-line-height-base: var(--line-height-relaxed, 1.6);
  --docs-line-height-tight: var(--line-height-tight, 1.3);
  --docs-line-height-normal: var(--line-height-normal, 1.45);
  font-family: var(--docs-font-family);
  font-size: var(--docs-font-size-base);
  line-height: var(--docs-line-height-base);
  display: grid;
  grid-template-columns: minmax(240px, 320px) 1fr;
  gap: var(--spacing-xxl, 2rem);
  padding: var(--spacing-xxxl, 3rem);
  background: var(--color-background);
  color: var(--color-text);
  min-height: 100vh;
  box-sizing: border-box;
}

.docs-page--centered {
  place-items: center;
  text-align: center;
}

.docs-page__toc {
  position: sticky;
  top: var(--spacing-xxl, 2rem);
  align-self: start;
  padding: var(--spacing-xxl, 1.5rem);
  border-radius: var(--border-radius-xl, 0.75rem);
  background: color-mix(in srgb, var(--color-surface) 92%, transparent);
  border: 1px solid color-mix(in srgb, var(--color-border) 55%, transparent);
  box-shadow: var(--shadow-md);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg, 1rem);
  overflow: visible;
}

.docs-page__toc::-webkit-scrollbar {
  width: 6px;
}

.docs-page__toc::-webkit-scrollbar-thumb {
  background: color-mix(in srgb, var(--color-border) 65%, transparent);
  border-radius: 999px;
}

.docs-page__toc-title-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--spacing-md, 0.75rem);
}

.docs-theme-toggle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--border-radius-md, 0.5rem);
}

.docs-page__toc-header h1 {
  margin: 0;
  font-size: var(--docs-font-size-xl);
  line-height: var(--docs-line-height-tight);
  font-weight: 600;
}

.docs-meta {
  margin: 0;
  font-size: var(--docs-font-size-sm);
  color: var(--color-textMuted);
  display: grid;
  gap: var(--spacing-sm, 0.5rem);
}

.docs-meta div {
  display: flex;
  gap: 0.5rem;
}

.docs-meta dt {
  font-weight: 600;
}

.docs-meta dd {
  margin: 0;
}

.docs-intro {
  margin: 0;
  font-size: var(--docs-font-size-base);
  line-height: var(--docs-line-height-base);
  color: var(--color-textSecondary);
}

.docs-toc-nav ol {
  margin: 0;
  padding: 0;
  display: grid;
  gap: 0.35rem;
  list-style: none;
  counter-reset: docs-toc-level-2;
}

.docs-toc-nav li {
  margin: 0;
  padding: 0;
  position: relative;
}

.docs-toc-nav li[data-level="2"] {
  counter-increment: docs-toc-level-2;
  padding-left: 2.75rem;
  border-radius: var(--border-radius-md, 0.5rem);
}

.docs-toc-nav li[data-level="2"]::before {
  content: counter(docs-toc-level-2) ".";
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  font-weight: 600;
  color: var(--color-textSecondary);
}

.docs-toc-nav li[data-level="3"],
.docs-toc-nav li[data-level="4"],
.docs-toc-nav li[data-level="5"],
.docs-toc-nav li[data-level="6"] {
  padding-left: 2.75rem;
  margin-left: 1.25rem;
  border-left: 1px solid color-mix(in srgb, var(--color-border) 45%, transparent);
}

.docs-toc-nav li[data-level="3"]::before,
.docs-toc-nav li[data-level="4"]::before,
.docs-toc-nav li[data-level="5"]::before,
.docs-toc-nav li[data-level="6"]::before {
  content: "â€¢";
  position: absolute;
  left: 1.25rem;
  top: 0.55rem;
  font-size: 0.55rem;
  color: var(--color-textMuted);
}

.docs-toc-nav li[data-level="3"] .docs-toc-link {
  font-size: var(--docs-font-size-sm);
  font-weight: 500;
}

.docs-toc-nav li[data-level="4"] .docs-toc-link,
.docs-toc-nav li[data-level="5"] .docs-toc-link,
.docs-toc-nav li[data-level="6"] .docs-toc-link {
  font-size: var(--docs-font-size-xs);
  font-weight: 500;
}

.docs-toc-link {
  display: inline-flex;
  align-items: center;
  width: 100%;
  color: inherit;
  text-decoration: none;
  font-weight: 600;
  transition: color var(--transition-default, 0.2s ease),
    border-color var(--transition-default, 0.2s ease);
  border-radius: var(--border-radius-sm, 0.375rem);
  padding-block: 0.35rem;
  padding-inline: 0.5rem;
  border-left: 2px solid transparent;
}

.docs-toc-link:hover,
.docs-toc-link:focus-visible {
  color: var(--color-primary);
  text-decoration: none;
  border-left-color: color-mix(in srgb, currentColor 40%, var(--color-border) 60%);
}

.docs-toc-nav a {
  color: inherit;
  text-decoration: none;
  font-weight: 500;
  transition: color var(--transition-default, 0.2s ease);
}

.docs-toc-nav a:hover,
.docs-toc-nav a:focus-visible {
  color: var(--color-primary);
  text-decoration: underline;
}


.docs-page__content-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.docs-page__content {
  display: grid;
  gap: var(--spacing-xxxl, 3rem);
  max-height: calc(100vh - 2 * var(--spacing-xxl, 2rem));
  overflow-y: auto;
  padding-right: var(--spacing-md, 1rem);
  scroll-behavior: smooth;
}

.docs-page__content::-webkit-scrollbar {
  width: 8px;
}

.docs-page__content::-webkit-scrollbar-thumb {
  background: color-mix(in srgb, var(--color-border) 50%, transparent);
  border-radius: 999px;
}

.docs-section {
  background: var(--color-surface);
  border-radius: var(--border-radius-xl, 1rem);
  padding: var(--spacing-xxl, 2rem) var(--spacing-xxxl, 2.5rem);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--color-borderLight);
  transition: background var(--transition-default, 0.2s ease),
    border-color var(--transition-default, 0.2s ease);
}

.docs-section__header h2 {
  margin: 0 0 0.5rem;
  font-size: var(--docs-font-size-xl);
  line-height: var(--docs-line-height-tight);
  font-weight: 600;
}

.docs-section__heading {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

.docs-section__toggle {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm, 0.5rem);
  width: 100%;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  font: inherit;
  text-align: left;
  color: inherit;
}

.docs-section__toggle:hover,
.docs-section__toggle:focus-visible {
  color: var(--color-primary, #2563eb);
}

.docs-section__toggle:focus-visible {
  outline: 2px solid var(--color-primary, #2563eb);
  outline-offset: 4px;
  border-radius: var(--border-radius-md, 0.5rem);
}

.docs-section__chevron {
  display: inline-flex;
  transform: rotate(0deg);
  transition: transform var(--transition-default, 0.2s ease);
}

.docs-section--collapsed .docs-section__chevron {
  transform: rotate(-90deg);
}

.docs-section__body[hidden] {
  display: none;
}

.docs-section__summary {
  margin: 0 0 var(--spacing-lg, 1rem);
  color: var(--color-textSecondary);
  font-size: var(--docs-font-size-base);
  line-height: var(--docs-line-height-normal);
}

.docs-paragraph {
  margin: var(--spacing-lg, 1rem) 0;
  line-height: var(--docs-line-height-base);
}

.docs-list {
  margin: var(--spacing-lg, 1rem) 0 var(--spacing-xl, 1.5rem);
  padding-left: 1.5rem;
  line-height: var(--docs-line-height-base);
}

.docs-note {
  --docs-note-accent: var(--color-info);
  --docs-note-surface: var(--color-infoLight);
  display: grid;
  grid-template-columns: auto 1fr;
  gap: var(--spacing-sm, 0.5rem) var(--spacing-lg, 1rem);
  align-items: baseline;
  border-left: 4px solid color-mix(in srgb, var(--docs-note-accent) 85%, transparent);
  border-radius: var(--border-radius-lg, 0.75rem);
  padding: var(--spacing-md, 0.75rem) var(--spacing-lg, 1rem);
  margin: var(--spacing-lg, 1rem) 0;
  background: var(--docs-note-surface);
  color: color-mix(in srgb, var(--docs-note-accent) 55%, var(--color-text) 45%);
  border: 1px solid color-mix(in srgb, var(--docs-note-accent) 30%, transparent);
}

.docs-note__label {
  font-weight: 600;
  color: color-mix(in srgb, var(--docs-note-accent) 70%, var(--color-text) 30%);
}

.docs-note__content {
  line-height: var(--docs-line-height-normal);
}

.docs-note--tip {
  --docs-note-accent: var(--color-success);
  --docs-note-surface: var(--color-successLight);
}

.docs-note--info {
  --docs-note-accent: var(--color-info);
  --docs-note-surface: var(--color-infoLight);
}

.docs-note--warning {
  --docs-note-accent: var(--color-warning);
  --docs-note-surface: var(--color-warningLight);
}

.docs-table-wrapper {
  overflow-x: auto;
  margin: var(--spacing-xl, 1.5rem) 0;
  border-radius: var(--border-radius-lg, 0.75rem);
  border: 1px solid color-mix(in srgb, var(--color-border) 50%, transparent);
  background: var(--color-surfaceWeak);
}

.docs-table {
  width: 100%;
  border-collapse: collapse;
  font-size: var(--docs-font-size-base);
}

.docs-table th,
.docs-table td {
  padding: 0.75rem 1rem;
  border-bottom: 1px solid color-mix(in srgb, var(--color-border) 40%, transparent);
  text-align: left;
}

.docs-table thead {
  background: var(--color-surfaceStrong);
  font-weight: 600;
}


.docs-subsection {
  margin-top: var(--spacing-xxl, 2rem);
  padding-top: var(--spacing-xl, 1.5rem);
  border-top: 1px solid color-mix(in srgb, var(--color-border) 30%, transparent);
}

.docs-subsection__header h3 {
  margin: 0 0 0.5rem;
  font-size: var(--docs-font-size-lg);
  line-height: var(--docs-line-height-tight);
  font-weight: 600;
}

.docs-resources {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 1rem;
}

.docs-resources a {
  font-weight: 600;
  color: var(--color-primary);
  text-decoration: none;
  transition: color var(--transition-default, 0.2s ease);
  font-size: var(--docs-font-size-base);
}

.docs-resources a:hover,
.docs-resources a:focus-visible {
  text-decoration: underline;
}

.docs-inline-code {
  font-family: "Source Code Pro", "Fira Code", monospace;
  background: var(--color-surfaceStrong);
  padding: 0.1rem 0.35rem;
  border-radius: 0.35rem;
  font-size: 0.95em;
}

.docs-emphasis {
  font-style: italic;
  color: inherit;
}

.docs-status {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-md, 0.75rem);
  padding: var(--spacing-lg, 1rem) var(--spacing-xl, 1.5rem);
  border-radius: 9999px;
  background: var(--color-surfaceStrong);
  color: var(--color-text);
  font-weight: 500;
  font-size: var(--docs-font-size-base);
}

.docs-status__spinner {
  animation: docs-spinner 1s linear infinite;
  font-size: 1.5rem;
}

.docs-status--error {
  background: var(--color-errorLight);
  color: var(--color-error);
}

@keyframes docs-spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 1024px) {
  .docs-page {
    grid-template-columns: 1fr;
    padding: var(--spacing-xxl, 2rem) var(--spacing-xl, 1.5rem);
  }

  .docs-page__toc {
    position: static;
    max-height: none;
  }

  .docs-page__content {
    max-height: none;
    overflow: visible;
    padding-right: 0;
  }
}

@media (max-width: 640px) {
  .docs-page {
    padding: var(--spacing-xl, 1.5rem) var(--spacing-lg, 1rem) var(--spacing-xxxl, 3rem);
  }

  .docs-section {
    padding: var(--spacing-xl, 1.5rem) var(--spacing-xxl, 2rem);
  }

  .docs-page__toc-header h1 {
    font-size: var(--docs-font-size-lg);
  }

  .docs-section__header h2 {
    font-size: var(--docs-font-size-lg);
  }

  .docs-subsection__header h3 {
    font-size: var(--docs-font-size-base);
  }
}
